.App {
  position: relative;
}
.App .title {
  width: 100%;
  padding: 40px 0px;
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  margin-bottom: 15px;
}
.App #scroll {
  display: none;
  position: fixed;
  right: 2.5vh;
  bottom: 2.5vh;
  cursor: pointer;
  width: 30px;
  height: 30px;
  background-color: black;
}
.App #scroll:hover {
  background-color: #c8c0c0;
  transition-duration: 1s;
}
